@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
body, html {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Roboto Mono', monospace;
}

.container {
  display: flex;
  height: 100%;
}

.container-left, .container-right {
  flex: 1 1;
  border: none;
  height: 58.5vh;
  padding-top: 20rem;
}

.container-left{
  background: yellow;
  color: hsl(190, 93%, 11%);
  font-size: 5rem;
  
}

.container-right {
  background: #022D36;
  font-size: 6rem;
  color: white;
  font-weight: bolder;
}

.btn{
  border-radius: 50%;
  background-color: yellow;
  color: #022D36;
  font-size: 5rem;
  padding: 25px 50px;
  margin: 10px 20px;
  text-shadow: 1px 1px rgb(72, 42, 243);
}


